import { FSComponent, DisplayComponent, VNode } from "msfssdk";
import "../../styles/efb.css";
// import { efbSettings, efbThemeSettings } from "../functions/settings";
export class BootPage extends DisplayComponent<any> {
  public render(): VNode {
    return (
      <div id="content" class="absolute-center">
        <svg class="circle-svg" viewBox="0 0 500 500">
          <defs>
            <path
              d="M50,250c0-110.5,89.5-200,200-200s200,89.5,200,200s-89.5,200-200,200S50,360.5,50,250"
              id="textcircle_top"
            >
              <animateTransform
                attributeName="transform"
                begin="0s"
                dur="20s"
                type="rotate"
                from="0 250 250"
                to="360 250 250"
                repeatCount="indefinite"
              />
            </path>
          </defs>
          <text class="circle-text" dy="70" textLength="1020">
            <textPath xlink:href="#textcircle_top">
              JPLogistics - JPLogistics -
            </textPath>
          </text>
        </svg>
      </div>
    );
  }
}
